@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');

@font-face {
    font-family: 'Choco cooky';
    font-style: normal;
    font-weight: normal;
    src: local('Choco cooky'), url('../fonts/Choco-Cooky/Chococooky.woff') format('woff');
}

@font-face {
    font-family: 'Cookies';
    font-style: normal;
    font-weight: normal;
    src: local('Cookies'), url('../fonts/Cookies/Cookies.ttf') format('woff');
}

/* reset */
*,
*::before,
*::after {
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul,
ol,
li {
    list-style: none;
}

img {
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: inherit;
    font-size: inherit;
}

img {
    pointer-events: none;
    -moz-user-select: none;
    -o-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

html,
body {
    height: 100%;
    line-height: 1;
    margin-top: -20px;
    scale: 95%;
}

:root {
    --font-family: "Cookies", sans-serif;
    --second-family: "Choco cooky", sans-serif;
    --third-family: "Inter", sans-serif;
}

body,
html {
    scroll-behavior: smooth;
    touch-action: none;
}

img {
    pointer-events: none;
}

::-webkit-scrollbar {
  width: 5px;
  background-color: #71b04700;
}

/* Track */
::-webkit-scrollbar-track {
  background-color: #4a80294d;
  border-radius: 1000px;
  margin-top: 50px;
  margin-bottom: 50px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #4a8029c9; 
  border-radius: 1000px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #4A8029; 
}