/* header */

.wrapper {
    padding-bottom: 50px;
}

.header {
    width: 100%;
}

.header__container {
    max-width: 1350px;
    width: 100%;
    padding: 0px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
}

.header__logo {
    z-index: 5;
}

.header__logoImage {}

.header__nav {
    display: flex;
    gap: 80px;
    z-index: 5;
}

.header__link {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 32px;
    color: #fff;
    transition: .15s;
    background-color: transparent;
    cursor: pointer;
}

.connect_wallet {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 32px;
    color: #fff;
    transition: .15s;
    background-color: transparent;
    cursor: pointer;
}

.header__link:hover {
    color: #F1C148
}

/* header cats */

.main {}

.main__cats_header {
    max-width: 1350px;
    padding: 0px 15px;
    width: 100%;
    margin: 0 auto;
    margin-top: 10px;
}

.main__cats_list {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.main__cat_item {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cat__headerImage {
    transition: .3s;
}

.main__cat_item:hover .cat__headerImage {
    animation: rotatingHeaderCat 2s infinite;
}

.cat__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    color: #fff;
    margin-top: 10px;
    text-align: center;
}

.cat__percent {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 24px;
    color: #fff;
}

.selectedCat .cat__title {
    color: #f1c148;
    -webkit-text-stroke: 1px #000;
}

.selectedCat .cat__percent {
    color: #f1c148;
    -webkit-text-stroke: 0.5px #000;
}

/* your cats */

.sectionSecond {
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
    max-width: 1350px;
    margin: 0 auto;
    margin-top: 30px;
}

.catsAndInventory {
    background-repeat: no-repeat;
    background-size: 100%;
    max-width: 870px;
    width: 100%;
    margin: 0 auto;
    padding: 25px 20px;
}

.yourCatsList {
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
    row-gap: 25px;
    padding: 10px 10px;
    overflow: hidden;
    overflow-y: auto;
    max-height: 450px;
    height: 450px;
}

.yourCat {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    justify-content: end;
    width: 196px;
    height: 208px;
    cursor: pointer;
}

.selectedCat .yourCat__title {
    -webkit-text-stroke: 1px #F1C148;
}

.selectedCat .yourCat__image {
    filter: drop-shadow(3px 3px 10px #F1C148);
}

.yourCat:hover .yourCat__image {
    transform: scale(1.05);
}

.yourCat__image {
    margin-left: -5px !important;
    margin-bottom: -40px !important;
    width: 200px !important;
    height: 202px !important;
    z-index: 1;
    transition: .1s;
}

.yourCat__shadow {
    width: 120px;
    height: 17px;
}

.yourCatShadowAndText {
    position: relative;
}

.yourCat__number {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 24px;
    text-align: right;
    color: #fff;
    -webkit-text-stroke: 0px #000;
    text-shadow: #000 2px 2px 1px;
    position: absolute;
}

.yourCat__number {
    top: 0;
}

.yourCat__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    text-align: center;
    color: #fff;
    text-align: center;
    /* margin-left: -66px; */
    margin-top: 5px;
}

/* inventory */

.inventory {
    /* background-image: url('./images/inventory/background.png'); */
    max-width: 407px;
    width: 100%;
    background-repeat: no-repeat;
    background-size: 100%;
    padding: 45px 30px;

}

.inventory__list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.inventory__item {
    width: 60px;
    height: 60px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.inventory__item:hover .inventory__image {
    transform: scale(1.1);
}


.inventory__image {
    transition: .15s;
}

.inventory__number {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 24px;
    text-align: right;
    color: #000;
    position: absolute;
    right: -5px;
    bottom: -10px;
}

/* buttons */

.buttons {
    display: flex;
    width: 100%;
    max-width: 1350px;
    padding: 0px 15px;
    margin: 0 auto;
    margin-top: 15px;
    gap: 50px;
}

.button__container {
    display: flex;
    align-items: center;
    position: relative;
}

.button__decoration {
    position: absolute;
}

.button__decoration2 {
    transform: rotate(180deg);
    position: absolute;
    right: 0;
}

.button {
    background-color: #fff;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 32px;
    color: #000;
    width: 270px;
    height: 50px;
    margin-left: 30px;
    margin-right: 35px;
    cursor: pointer;
}

.claimVestedTest {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    margin-top: 20px;
    color: #F1C148;
    -webkit-text-stroke: 1px #000;
    cursor: pointer;
}

.button:hover {
    background-color: #F1C148;
}

.jackpotContainer {
    display: flex;
    justify-content: center;
    margin-top: 15px;
}

.jackpotLineLeft {
    rotate: 180deg;
}

.jackpotText {
    width: 510px;
    font-family: var(--font-family);
    color: #fff;
    font-size: 24px;
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.jackpotLineRight {}

.rightSection3 {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.gameResult {
    height: 80px;
    background-color: #FFE298;
    color: #F1C148;
    font-family: var(--font-family);
    font-size: 50px;
    -webkit-text-stroke: 3px #000;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 2em;
    width: 270px;
    margin: 0 auto;
    border: 5px solid #000;
    position: relative;
}

.gameResult>span {
    z-index: 2;
}

.gameResult::after {
    content: '';
    background-color: #CFB678;
    height: 40px;
    width: 220px;
    border-radius: 2em;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    z-index: 1;
}

.inventoryText {
    color: #fff;
    font-family: var(--second-family);
    max-width: 417px;
    text-align: center;
    font-size: 20px;
    line-height: 120%;
}

@keyframes rotatingHeaderCat {
    0% {
        transform: rotate(0deg);
    }

    25% {
        transform: rotate(-10deg);
    }

    50% {
        transform: rotate(0deg);
    }

    75% {
        transform: rotate(10deg);
    }

    100% {
        transform: rotate(0deg);
    }
}